import React from "react";
import { Row } from "react-bootstrap";
import { useParams } from "react-router-dom";
import { desarrollos } from "../utils/constants";
import "./Detalle.scss";

const Detalle = () => {
  const params = useParams();
  const detalleTitle = params.category;
  const itemDesarrollos = desarrollos.find((i) => i.title === detalleTitle);

  return (
    <section className="sec_detalle">
      <Row className="row_img-ext">
        <img src={itemDesarrollos.img} alt="" className="img_ext" />
        <h1>{itemDesarrollos.title}</h1>
        <h6>{itemDesarrollos.category_title}</h6>
        <h6>{itemDesarrollos.direccion}</h6>
      </Row>
      <Row className=""></Row>
    </section>
  );
};

export default Detalle;
