import React, { useEffect } from "react";
import { Col, Row } from "react-bootstrap";
import { useParams } from "react-router-dom";
import ElementoDesarrollo from "../../Home/DesarrollosSec/ElementoDesarrollo/ElementoDesarrollo";
import { desarrollos } from "../../utils/constants";
import "./Despliegue.scss";

const Despliegue = () => {
  const params = useParams();
  const category = params.category;
  let filtrado = desarrollos.filter((i) => i.category === category);

  useEffect(() => {
    return () => {
      filtrado = desarrollos.filter((i) => i.category === category);
    };
  }, [category]);

  return (
    <Row className="row_despliegue">
      {filtrado.map((el, i) => (
        <Col xs={6}>
          {(i / 2) % 2 === 0 || i === 1 ? (
            <ElementoDesarrollo
              title={el.title}
              category={el.category_title}
              direccion={el.direccion}
              img={el.img}
              orientation="izq"
            />
          ) : (
            <ElementoDesarrollo
              title={el.title}
              category={el.category_title}
              direccion={el.direccion}
              img={el.img}
              orientation="der"
            />
          )}
        </Col>
      ))}
    </Row>
  );
};

export default Despliegue;
