import React, { useEffect, useState } from "react";
import { Col, Row } from "react-bootstrap";
import "./DesarrollosSec.scss";
import { desarrollos } from "../../utils/constants";
import ElementoDesarrollo from "./ElementoDesarrollo/ElementoDesarrollo";

const DesarrollosSec = () => {
  return (
    <section className="sec-desarrollos">
      <Row>
        <Col xs={6} className="col-espacio">
          <Col xs={6} className="col-desarrollos">
            <h2 className="h2-titulo">Desarrollos</h2>
          </Col>
        </Col>
        <Col className="col-espacio"></Col>
      </Row>
      <Row>
        {desarrollos.map((el, i) => (
          <Col xs={6}>
            {(i / 2) % 2 === 0 || i === 1 ? (
              <ElementoDesarrollo
                title={el.title}
                category={el.category}
                direccion={el.direccion}
                img={el.img}
                orientation="izq"
              />
            ) : (
              <ElementoDesarrollo
                title={el.title}
                category={el.category}
                direccion={el.direccion}
                img={el.img}
                orientation="der"
              />
            )}
          </Col>
        ))}
      </Row>
    </section>
  );
};

export default DesarrollosSec;
